@font-face {
  font-family: "Open Sans Regular";
  src: url("../fonts/opensans-regular.woff2") format("woff2"), url("../fonts/opensans-regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "Open Sans Semibold";
  src: url("../fonts/opensans-semibold.woff2") format("woff2"), url("../fonts/opensans-semibold.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "Open Sans Bold";
  src: url("../fonts/opensans-bold.woff2") format("woff2"), url("../fonts/opensans-bold.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

body {
  font: 400 18px/1.4 'Open Sans Regular', sans-serif;
  color: #959595;
  background-color: #d50000;
  text-align: center;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

a {
  text-decoration: none;
  color: #fff;
}

.btn,
.btn-default {
  font: 400 20px/1.4 'Open Sans Bold', sans-serif;
  text-align: center;
  border-radius: 30px;
  display: inline-block;
  background-color: #d50000;
  padding: 7px 59px;
  transition: background-color 0.25s;
}

.btn:hover,
.btn-default:hover {
  background-color: #ef0000;
}

.btn:active,
.btn-default:active {
  transform: translateY(1px);
}

.btn_position {
  position: relative;
  z-index: 1;
}

.btn-default {
  color: #fff;
}

.btn-scroll {
  font: 0/0 a;
  text-shadow: none;
  color: transparent;
  padding: 10px;
  display: inline-block;
}

.btn-scroll_position {
  position: relative;
  z-index: 1;
}

.btn-scroll:hover .icon {
  transform: translateY(5px);
}

.btn-scroll .icon {
  font-size: 20px;
  color: #d50000;
  transition: transform 0.25s;
}

.btn-more {
  text-align: center;
  font: 400 16px/1.4 'Open Sans Bold', sans-serif;
  color: #d50000;
  text-decoration: none;
  display: inline-block;
  cursor: pointer;
  position: relative;
}

.btn-more:after {
  content: '';
  display: block;
  position: absolute;
  left: 0;
  right: 0;
  top: 100%;
  margin: -2px 0 0;
  height: 1px;
  background-color: #d50000;
  transition: opacity 0.25s, color 0.25s;
  opacity: 1;
}

.btn-more:hover:after {
  opacity: 0;
}

.btn-more:active {
  top: 1px;
}

.title {
  font: 400 46px/1.2 'Open Sans Regular', sans-serif;
  color: #fff;
  position: relative;
  z-index: 1;
}

.input {
  display: block;
  width: 100%;
  background-color: #fff;
  border: 2px solid #bdbdbd;
  border-radius: 50px;
  color: #bdbdbd;
  font: 400 20px/1.2 'Open Sans Regular', sans-serif;
  padding: 16px 30px;
}

.input::placeholder {
  color: #bdbdbd;
}

.inner {
  max-width: 1200px;
  padding: 0 10px;
  margin: 0 auto;
}

header {
  padding: 60px 0 0;
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  z-index: 2;
}

header .inner {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.logo {
  font: 0/0 a;
  text-shadow: none;
  color: transparent;
  background: url(../img/logo.svg) no-repeat center;
  width: 180px;
  height: 85px;
}

.nav {
  display: flex;
}

.nav__link {
  padding: 10px 28px;
  display: block;
  font-family: 'Open Sans Semibold', sans-serif;
  transition: color 0.25s;
  position: relative;
  overflow: hidden;
}

.nav__link:after {
  content: '';
  display: block;
  left: 28px;
  bottom: 4px;
  width: calc(100% - 56px);
  height: 2px;
  background-color: #d50000;
  position: absolute;
  transform: scaleX(0);
  transition: transform 0.25s;
}

.nav__link:hover {
  color: #d50000;
}

.nav__link:hover:after {
  transform: scaleX(1);
}

.nav__link:last-of-type {
  padding: 10px 0 10px 28px;
}

.nav__link:last-of-type:after {
  right: 0;
  width: calc(100% - 28px);
}

.company {
  background-color: #000;
  position: relative;
}

.company .inner {
  height: 100vh;
  min-height: 700px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.company-content {
  padding: 264px 0 0;
}

.company-content__title {
  color: #fff;
  font-size: 46px;
  line-height: 1.25;
}

.company-content__btn {
  padding: 55px 0 0;
}

.company-content__bottom {
  padding: 22px 0 0;
  color: #d9d9d9;
}

.company-content__scroll-btn {
  padding: 80px 0 0;
}

.company-content__txt_position {
  position: relative;
  z-index: 1;
}

.services {
  padding: 50px 0;
  background-color: #fff;
  position: relative;
}

.services .title {
  color: #424242;
}

.services-list {
  padding: 70px 0 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.services-list__item {
  width: 32%;
  min-height: 456px;
}

@media only screen and (max-width: 900px) {
  .services-list__item {
    width: 49%;
    margin: 2% 0 0;
  }
}

@media only screen and (max-width: 600px) {
  .services-list__item {
    width: 100%;
    min-height: auto;
    margin: 20px 0 0;
  }
}

.services-cont {
  font-size: 16px;
  line-height: 1.75;
  color: #959595;
}

.services-cont__top {
  margin: 20px 0 0;
}

.services-cont__body {
  display: none;
}

.services-cont__bottom {
  margin: 26px 0 0;
}

.services-cont__more {
  margin: 30px auto 0;
}

.s-item {
  background-color: #fff;
  padding: 18px 18px 30px;
  box-shadow: 0px 10px 40px #d9d9d9;
  border-radius: 3px;
}

.s-item__ico {
  width: 160px;
  height: 160px;
  margin: auto;
}

.s-item__title {
  margin: 8px 0 0;
  font: 20px/26px 'Open Sans Bold', sans-serif;
  color: #4f4f4f;
  min-height: 78px;
}

.s-item__txt {
  margin: 26px 0 0;
  color: #959595;
  font: 18px/28px 'Open Sans Regular', sans-serif;
}

.products {
  padding: 130px 0 170px;
  background-color: #d50000;
}

.products-list {
  padding: 70px 0 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.products-list__item {
  width: 47%;
  min-height: 228px;
}

.p-item {
  background-color: #fff;
  text-align: left;
  padding: 45px 54px;
}

.p-item__title {
  color: #d50000;
  font: 22px 'Open Sans Bold', sans-serif;
}

.p-item__txt {
  margin: 20px 0 0;
  font: 400 18px/28px Arial, sans-serif;
}

.contact {
  padding: 150px 0;
  position: relative;
}

.contact:before {
  content: '';
  display: block;
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  height: 315px;
  background: url(../img/contact-bg.svg) no-repeat top center;
  background-size: cover;
  z-index: -1;
}

.contact:after {
  content: '';
  display: block;
  position: absolute;
  left: 0;
  right: 0;
  top: 300px;
  bottom: 0;
  z-index: -1;
  background-color: #fff;
}

.contact__in {
  max-width: 505px;
}

.contact .title {
  color: #424242;
}

.form {
  padding: 50px 0 0;
}

.form__row:not(:first-of-type) {
  margin: 20px 0 0;
}

.form__btn {
  margin: 50px 0 0;
}

#particles-js {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 0;
}
